import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import CircularProgress from '@material-ui/core/CircularProgress';
import { useState }from "react";
import axios from "axios";
import LaunchIcon from '@material-ui/icons/Launch';

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    flexWrap: 'nowrap',
    // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
    transform: 'translateZ(0)',
  },
  title: {
    color: 'white',
    fontSize:'20px',
    fontWeight:'whitesmoke'
  },
  titleBar: {
    background:
      'linear-gradient(to top, rgba(0,0,0,0.7) 0%, rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
  },
  card:{
    
    border:'5px solid lightgray',
    borderRadius: '10px',
    textOrientation:'center',
    textAlign:'center',
    backgroundColor:'sandybrown',
    margin:'auto',
    width:'1000px'
  },
  cardTitle:{
    margin:'5px'
  },
  divisor:{
    marginBottom:'40px'
  },
}));


export default function FoodList(props) {
  const classes = useStyles();
  const [food,setFood] = useState([]);
  const [loaded,setLoaded] = useState(false);
  

  useEffect(()=>{
    axios.get('https://www.themealdb.com/api/json/v1/1/filter.php?c='+props.category)
    .then((data)=>{
      setFood(data.data.meals);
      setLoaded(true);
    }).catch((error)=>{
      console.log(error);
    })
    
  },[]);


  return (
    <div className={classes.divisor}>
      <div className={classes.card}>
        <h2 className={classes.cardTitle}>{props.category}</h2>
          {loaded ? <div className={classes.root}>
            <GridList className={classes.gridList} cols={2.5}>
              {food.map((tile) => (
                <GridListTile key={tile.idMeal}>
                  <img src={tile.strMealThumb} alt={tile.strMeal} />
                  <GridListTileBar
                    title={tile.strMeal}
                    classes={{
                      root: classes.titleBar,
                      title: classes.title,
                    }}
                    actionIcon={
                      <IconButton  aria-label={`star ${tile.strMeal}`}>
                        <LaunchIcon className={classes.title} />
                      </IconButton>
                    }
                  />
                </GridListTile>
              ))}
          </GridList>
          </div>: <CircularProgress color="secondary"/>}
      </div>
    </div>
  );
}